'use client';
import { useState } from 'react';
import {useMove} from 'react-aria';
import ShawnLawn from "@/app/components/shawn-lawn";

export default function Header({ className }: { className?: string }) {

    let [events, setEvents] = useState([]);
    let [headerVisible, setHeaderVisiblity] = useState(false);

    let { moveProps } = useMove({
        onMoveStart(e) {
            setEvents(
                (events) => [
                    `move start with pointerType = ${e.pointerType}`,
                    ...events
                ]
            );
        },

        onMove(e) {
            setHeaderVisiblity(() => {
                console.log(e.deltaY);
                return(e.deltaY <= 0);
            });
            setEvents(
                (events) => [`move end with pointerType = ${e.pointerType}`, ...events]
            );
        }
    });

    const stickyBehaivor = headerVisible?`sticky top-0`:``;

    return (
        <>
            <header className={`${className} container w-full w-full w-full md:flex md:flex-row justify-center md:justify-start items-center mx-auto bg-white-3 dark:bg-black-3 dark:text-white-1 sticky top-0 ${stickyBehaivor}`}>
                <ShawnLawn />
                {/*
                    TODO: #35 Add nav links
                    TODO: #34 Extract nav-links into separate component 
                 */}
            </header>
        </>
    );
}